
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { validateNotNull } from "util/rules";

export default {
data() {
return {
graduationStatus: "",
graduationInfo: {},
progressArr: [
{
label: "提交",
value: 0,
},
{
label: "PCD",
value: 2,
},
{
label: "PM",
value: 2.1,
},
{
label: "区域",
value: 3,
},
{
label: "渠道",
value: 4,
},
{
label: "完成",
value: 4,
},
],
userInfo: {},
reasonText: "",
cargoHandlingText: "",
reason: "",
cargoHandling: "",
closeDate: "",
graduation_reason: [],
cargo_handling: [],
startDate: new Date().format("yyyy-MM-dd"),
endDate: new Date(new Date().getTime() + 29 * 1000 * 60 * 60 * 24).format(
"yyyy-MM-dd"
),
ifReason: false,
ifCargo: false,
inited: false, // 防止狂点
};
},
methods: {
goBack() {
this.$util.getMachine(4);
},
showSelector() {
this.ifReason = true;
},
showCargoSelector() {
this.ifCargo = true;
},
setReason(key, item) {
this.reasonText = item.label;
this.reason = key;
},
setCargo(key, item) {
this.cargoHandlingText = item.label;
this.cargoHandling = key;
},
resubmit() {
this._getDicts();
this.graduationStatus = false;
},
submit() {
if (this.inited) {
this.$vux.toast.text("拼命请求中，请稍候~");
return;
}
if (
validateNotNull(this, this.reason, "关店原因") &&
validateNotNull(this, this.cargoHandling, "关店后货物处理") &&
validateNotNull(this, this.closeDate, "实际关店日期")
) {
this.inited = true;
this.$tupHttp(this, "/front/user/graduation/submit", "POST", {
reason: this.reason,
cargoHandling: this.cargoHandling,
closeDate: this.closeDate,
})
.then(
(res) => {
this.$vux.toast.text("提交成功！");
setTimeout(() => {
this.$router.go(-1);
}, 500);
},
() => {
this.inited = false;
}
)
.catch(() => {
this.inited = false;
});
}
},
_graduationInfo() {
this.$tupHttp(this, "front/user/graduation/info").then((res) => {
if (res.data.model && !res.data.model.overReCommit) {

this.graduationStatus = true;
let auditStatus = res.data.model.auditStatus;
if (auditStatus === 2 || auditStatus === 8) {
auditStatus = 2.1;
} else if (auditStatus === 7) {
auditStatus = 2;
} else if (auditStatus === -7) {
auditStatus = -2;
} else if (auditStatus === -8) {
auditStatus = -2.1;
}
res.data.model.auditStatus = auditStatus;
this.graduationInfo = res.data.model;
} else {

this._getDicts();
this.graduationStatus = false;
}
});
},
_getInfo() {
this.$tupHttp(this, "front/user/info/v1").then((res) => {
this.userInfo = res.data.model;
this.userInfo.pOpenningDate = this.userInfo.pOpenningDate
? new Date(this.userInfo.pOpenningDate).format("yyyy-MM-dd")
: this.userInfo.pOpenningDate;
this.userInfo.pFirstorderDate = this.userInfo.pFirstorderDate
? new Date(this.userInfo.pFirstorderDate).format("yyyy-MM-dd")
: this.userInfo.pFirstorderDate;
this.userInfo.pGraduationDate = this.userInfo.pGraduationDate
? new Date(this.userInfo.pGraduationDate).format("yyyy-MM-dd")
: this.userInfo.pGraduationDate;
});
},
_getDicts() {
let data = ["graduation_reason_new", "cargo_handling"];
this.$tupHttp(this, "front/dict/getDicts", "POST", data).then((res) => {
this.graduation_reason = res.data.model.graduation_reason_new.map(
(item, index) => {
let sItem = {};
sItem.label = item.dictName;
sItem.value = item.dictValue;
return sItem;
}
);
this.cargo_handling = res.data.model.cargo_handling.map(
(item, index) => {
let sItem = {};
sItem.label = item.dictName;
sItem.value = item.dictValue;
return sItem;
}
);
});
},
},
created() {
this._graduationInfo();
this._getInfo();
},
components: {
XHeader,
Cell,
Actionsheet,
Datetime,
},
};



